@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css";
@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700);
.navbar {
  background-color: #21252D;
  border-radius: 0px;
}

@media only screen and (min-width: 992px) {
  .navbar {
    padding: .7rem 3.4rem;
  }
}

@media only screen and (max-width: 992px) {
  .navbar .navbar-brand {
    display: none;
  }
}

.navbar-light .navbar-toggler {
  background-image: url(../images/icons/escores.png);
  background-size: 100%;
  border-color: transparent;
}

.navbar-light .navbar-toggler .white {
  color: #FFFFFF;
  font-size: 1.6rem;
}

@media only screen and (min-width: 992px) {
  .navbar-light .navbar-nav .nav-item {
    float: right;
    margin-left: 1rem;
    margin-top: 3px;
  }
}

@media only screen and (max-width: 992px) {
  .navbar-light .navbar-nav .nav-item {
    border-top: solid 2px;
    padding: 4px 0px 4px 0px;
  }
  .navbar-light .navbar-nav .nav-item span {
    margin-left: 1rem;
  }
  .navbar-light .navbar-nav .nav-item:nth-child(1) {
    border-top: none;
  }
}

.navbar-light .navbar-nav .nav-item .white {
  color: #848484;
}

.navbar-light .navbar-nav .nav-item span:hover .white {
  cursor: pointer;
  color: #FFFFFF;
}

.navbar-light .navbar-nav .nav-link {
  cursor: none;
  display: inline-block;
  font-family: arial;
  font-size: .9rem;
}

.middle {
  height: 75vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-flex-direction: column;
  flex-direction: column;
}

.middle .panel-default {
  height: 450px;
  width: 100%;
  background-color: #FFFFFF;
  -webkit-box-shadow: 1px 1px 8px #C5C6C7;
  -moz-box-shadow: 1px 1px 8px #C5C6C7;
  box-shadow: 1px 1px 8px #C5C6C7;
  font-family: lato;
  font-weight: bold;
}

@media only screen and (min-width: 544px) {
  .middle .panel-default {
    width: 350px;
    height: 450px;
  }
}

.middle .panel-default .panel-heading {
  text-align: center;
  padding-top: 30px;
  font-family: arial;
}

.middle .panel-default .panel-heading h3 {
  color: #515151;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0px;
  text-transform: uppercase;
}

.middle .panel-default .panel-heading p {
  color: #C5C6C7;
  font-weight: normal;
  font-size: 1rem;
  letter-spacing: 1px;
}

.middle .panel-default .panel-body {
  margin-top: 60px;
}

.middle .panel-default .panel-body .panel-wrapper {
  padding: 0px 30px 0px 30px;
}

.middle .panel-default .panel-body .panel-wrapper input {
  font-size: 1rem;
  font-weight: normal;
  margin-top: 7px;
  margin-bottom: 7px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  border: none;
  background-color: #ececed;
  color: #898989;
}

.middle .panel-default .panel-body .panel-wrapper .checkbox {
  color: #515151;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  margin-top: 10px;
}

.middle .panel-default .panel-body .panel-wrapper .checkbox label {
  cursor: pointer;
}

.middle .panel-default .panel-body .panel-wrapper .checkbox label [type="checkbox"] {
  display: none;
}

.middle .panel-default .panel-body .panel-wrapper .checkbox label [type="checkbox"] + .item-text:before {
  content: "\f096";
  font-family: FontAwesome;
  speak: none;
  font-style: normal;
  font-weight: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  width: 1em;
  display: inline-block;
  margin-right: 3px;
  color: #636363;
}

.middle .panel-default .panel-body .panel-wrapper .checkbox label [type="checkbox"]:checked + .item-text:before {
  content: "\f14a";
  color: #636363;
  animation: tick 170ms ease-in;
}

.middle .panel-default .panel-body .panel-wrapper .button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
}

.middle .panel-default .panel-body .panel-wrapper .button button {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  width: 100px;
  margin-top: 0px;
  background: #00AA58;
  /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left, #00AA58, #00A6B9);
  /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(right, #00AA58, #00A6B9);
  /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(right, #00AA58, #00A6B9);
  /* For Firefox 3.6 to 15 */
  background: linear-gradient(to right, #00AA58, #00A6B9);
  /* Standard syntax */
  border: none;
}

.middle .panel-default .panel-body .panel-wrapper .forgot {
  text-align: center;
  margin-top: 20px;
  color: #3266AD;
  font-size: .9rem;
}

@keyframes tick {
  0% {
    transform: scale(0);
  }
  90% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}

body {
  background-image: url("../images/bg.png");
}
